import React from "react";
import { newContextComponents } from "@drizzle/react-components";
var hexToBinary = require('hex-to-binary');
var toInteger = require('to-integer');
const { AccountData, ContractData, ContractForm } = newContextComponents;

function renderPoem(hexPoem) {
  //pop 11 bits, convert to integer, lookup dictionary array
  //pop 3 bits, convert to integer, lookup punctuation array
  //repeat until nothing to pop 
  
  var poemNoPrefix = hexPoem.slice(2);
  var binaryPoem = hexToBinary(poemNoPrefix);
  console.log(binaryPoem.slice(0,11));
  const wholePoem = [];
  for(var i=0; i < 242; i += 14) {
  wholePoem.push(dictionaries.wordDictionary[toInteger("0b" + binaryPoem.slice(i + 0, i +11))]);
  wholePoem.push(dictionaries.punctDictionary[toInteger("0b" + binaryPoem.slice(i + 11, i + 14))]);
  }
  return wholePoem.join("");


}

//Turn the poems into buttons
//split out each poem as its own react component
//button tag
//onclick handler
//call the function using drizzle.web3

function renderWords(displayData) {
  return <div><h2><pre>{"Poem " + displayData.IDofFirstPoem + ": " + renderPoem(displayData.poemA)}</pre>
  <br/> 
  <pre>{"Poem " + displayData.IDofSecondPoem + ": " + renderPoem(displayData.poemB)}</pre></h2></div>;
}

export default ({ drizzle, drizzleState }) => {
  // destructure drizzle and drizzleState from props
  console.log(drizzle);
  return (
    <div className="App">

      {/* <div className="section">
        <h2>Active Account</h2>
        <AccountData
          drizzle={drizzle}
          drizzleState={drizzleState}
          accountIndex={0}
          units="ether"
          precision={3}
        />
      </div> */}

      <div className="section">

        <p>
          <h1>Appraise These Poems:</h1>

          <ContractData
            drizzle={drizzle}
            drizzleState={drizzleState}
            contract="Universe"
            method="getTwoPoems"
            render={renderWords}
          />

        </p>
        <h1>Now Select One Poem Over Another:</h1>
        <ContractForm 
          drizzle={drizzle} 
          drizzleState={drizzleState}
          contract="Universe" 
          method="selectPoem" 
          sendArgs=""
          labels={["Selected Poem", "Rejected Poem"]} 
        />

        <h2>You can find the dictionary here:</h2>

          <ContractData
            drizzle={drizzle}
            drizzleState={drizzleState}
            contract="Universe"
            method="getDictionary"

          />
      </div>
    </div>
  );
};

